/**
 * Auto-categorize a transaction based on its name/description
 * Returns the best matching category or "Uncategorized"
 */
export function autoCategorize(name: string): Category {
    const lowercaseName = name.toLowerCase();

    // Check each category's keywords
    for (const [category, keywords] of Object.entries(CATEGORY_KEYWORDS)) {
        for (const keyword of keywords) {
            if (lowercaseName.includes(keyword.toLowerCase())) {
                return category as Category;
            }
        }
    }

    return "Uncategorized";
}
